* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

body {
  background-color: #f2f2f2;
  overflow-x: hidden;
}

/*Font Styling for browser compatibility*/

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}
